.container.contact__container {
    width:58%;
    display: grid;
    grid-template-columns: 30% 58%;
    gap:12%;
}
.contact__options{
    display: flex;
    flex-direction: column; 
    gap: 1.5rem;
     margin-top: 10px;
}
.contact_option{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
    gap:1.2rem;
}
.contact_option:hover{
    background: transparent;
    border-color: var(--color-primary-varient);
}
.contact__options-icon{
    font-size:1.5rem;
    margin-bottom: 0.5rem;
}
.contact_option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size:0.8rem;

}
/* ================================ */
form{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}
input,textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border:2px solid var(--color-primary-varient);
    resize: none;
    color:var(--color-white)
}

@media screen and (max-width:600px){
    .container.contact__container{
        width:200%;
        margin: auto 3rem;
        justify-content: center;
        display: inline;
    }
    .contact__options{
        display: grid;
        grid-template-columns:90%;
        margin-left:50px;
        gap: 5%;
        margin-bottom:50px;
    }
    input,textarea{
        margin-top:50px;
        width:50%;
    margin-left: 50px;
    }
    #contact{
        margin-top: 800px;
    }
 }